import { isFunction, find, isEmpty } from 'lodash';
import { adaptCallbackForLegacyAPI } from '@devsnc/sn-controls-common';

export const actionHandlers = {
	'NOW_TYPEAHEAD#VALUE_SET': ({
		action,
		properties,
		updateState,
		updateProperties,
		dispatch
	}) => {
		const { value } = action.payload;
		const { name, onChange, onStagedValueChange } = properties;

		// faking event because app teams are expecting event as param with some properties in OnChange callback
		const event = {
			target: {
				value: value
			},
			persist: () => {
				return;
			},
			stopPropagation: () => {
				return;
			}
		};

		if (isFunction(onStagedValueChange)) {
			adaptCallbackForLegacyAPI(onStagedValueChange)({ value });
		}
		dispatch('NOW_RECORD_TYPEAHEAD#STAGED_VALUE_CHANGED', {
			name,
			value,
			event
		});

		if (isFunction(onChange)) {
			onChange(event);
		} else {
			updateProperties({ value });
		}

		updateState({ currentDisplayValue: value });
	},
	'NOW_TYPEAHEAD#SELECTED_ITEM_SET': ({
		action,
		properties,
		dispatch,
		updateState,
		updateProperties,
		state
	}) => {
		const { value } = action.payload;
		const { name, onValueChange, options, recents } = properties;

		if (value) {
			//Handle Value Changes on selecting an item from the dropdown
			const itemDisplayValue = find([...options, ...recents], {
				value: value
			}).displayValue;

			updateState({ currentDisplayValue: itemDisplayValue });

			if (isFunction(onValueChange)) {
				adaptCallbackForLegacyAPI(onValueChange)({
					value,
					displayValue: itemDisplayValue
				});
			} else {
				// Update selectedItem to value so NDS typeahead know value is set and not to trigger a invalid state
				updateProperties({ value: itemDisplayValue, selectedItem: value });
			}
			// set timeout to ensure staged value change dispatches before value change
			setTimeout(() => {
				dispatch('NOW_RECORD_TYPEAHEAD#VALUE_CHANGED', {
					name,
					value,
					displayValue: itemDisplayValue
				});
			}, 0);
		} else {
			// set timeout to ensure staged value change dispatches before value change
			setTimeout(() => {
				dispatch('NOW_RECORD_TYPEAHEAD#INTERNAL_VALUE_CHANGE', {
					name,
					value
				});
			}, 0);
			updateProperties({ selectedItem: value });
		}
	},

	'NOW_RECORD_TYPEAHEAD#INTERNAL_VALUE_CHANGE': {
		stopPropagation: true,
		effect: ({ dispatch, state, action }) => {
			const { value, name } = action.payload;
			dispatch('NOW_RECORD_TYPEAHEAD#VALUE_CHANGED', {
				name,
				value,
				displayValue: state.currentDisplayValue
			});
		}
	},

	'NOW_TYPEAHEAD#INTERACTION_FINISHED': ({ updateState, properties }) => {
		const { onBlur } = properties;
		if (isFunction(onBlur)) {
			onBlur(event);
			updateState({
				initialSearchComplete: false
			});
		}
	},

	'NOW_POPOVER#CONTENT_VISIBLE': ({ updateState, state }) => {
		const { searchOnClick, onClick } = state.properties;
		const { initialSearchComplete } = state;

		if (searchOnClick) {
			if (isFunction(onClick) && !initialSearchComplete) {
				onClick();
				// This is done to prevent re-searching when popover opens again
				updateState({
					initialSearchComplete: true
				});
			}
		}
	}
};
