import PropTypes from 'prop-types';
import React from 'react';
import { createCustomElement } from '@servicenow/ui-core';
import react from '@servicenow/ui-renderer-react';
import {
	isFunction,
	isEmpty,
	toString,
	isNil,
	isNumber,
	filter,
	isObject
} from 'lodash';
import { t } from 'sn-translate';
import '@servicenow/now-typeahead';
import { actionHandlers } from './actionHandlers';
// Function imports
import {
	controlledFieldPropTypes,
	isAttrTrue,
	withValueChangeCallbacks,
	transformState,
	ForwardProperties,
	transformMessages,
	getMatchesCount
} from '@devsnc/sn-controls-common';

// Style imports
import style from './reference.scss';

/**
 * Reference fields allow users to select a value from a dynamic list of
 * choices. Unlike a reference list, a user cannot select multiple values.
 */
export class NowTypeaheadBase extends React.PureComponent {
	static componentName = 'now-record-typeahead';

	static actionHandlers = actionHandlers;

	/**
	 * render
	 * @return {ReactElement} markup
	 */
	render() {
		const {
			name,
			readonly,
			placeholder,
			label,
			messages,
			description,
			invalid,
			required,
			reversed,
			options,
			recents,
			autofocus,
			configMessages,
			loading,
			totalMatchesCount,
			secondaryLabelDelimiter,
			dispatch,
			formData,
			dAProps,
			disabled,
			loadingMessage,
			maxSearchMatches,
			search,
			selectedItem
		} = this.props;

		const { totalMatchesCountHeaderMessage } = getConfigMessages(
			this.props,
			configMessages
		);
		const value = toString(this.props.value);

		const makeItemsObject = (options, headerMessage) => {
			return {
				label: headerMessage,
				children: options.map(option => {
					return {
						id: option.value,
						label: option.displayValue || t('(empty)'),
						sublabel: filter(option.secondary)
							.map(secondaryValue => {
								if (isObject(secondaryValue)) {
									return secondaryValue.value;
								}
								return secondaryValue;
							})
							.join(
								secondaryLabelDelimiter !== undefined
									? secondaryLabelDelimiter
									: ' | '
							)
					};
				})
			};
		};

		const totalMatchesCountHeaderMSG = getHeaderMessage(
			totalMatchesCountHeaderMessage,
			options,
			totalMatchesCount,
			maxSearchMatches,
			search,
			value
		);

		const recentItems = isEmpty(recents)
			? {}
			: makeItemsObject(recents, t('Recent Selections'));
		const searchItems = isEmpty(options)
			? {}
			: makeItemsObject(options, totalMatchesCountHeaderMSG);

		const optionsNew = [recentItems, searchItems].filter(obj => !isEmpty(obj));

		return (
			<now-typeahead
				name={name + '_input'}
				items={
					loading
						? [
								{
									label: loadingMessage || t('Searching...'),
									children: []
								}
						  ]
						: optionsNew
				}
				label={label}
				value={value}
				required={required}
				reversed={reversed}
				readonly={isAttrTrue(readonly)}
				placeholder={placeholder}
				disabled={disabled}
				messages={transformMessages(messages)}
				invalid={invalid}
				autofocus={autofocus}
				manageSelectedItem={true}
				helperContent={description}
				// now-typeahead does triple equal check for selected item being null
				// before dispatching selected_item_set. This ensures we only pass null
				// to prevent any dispatches for values such as empty string or undefined
				selectedItem={selectedItem || null}
				// this prop was added if user doesn't want to implement filtering logic
				// it accepts value "initial" for showing items that begins with input value
				// it accepts value "contains" for showing items that contains input value
				search={search}
			>
				{dAProps ? (
					<sn-declarative-field-action
						slot="end"
						model={dAProps}
						actions={dAProps.declarativeUiActions}
						dispatch={dispatch}
						formData={formData}
						fetchDeclarativeActions={dAProps.fetchDeclarativeActions}
					/>
				) : null}
			</now-typeahead>
		);
	}
}

function getHeaderMessage(
	totalMatchesCountHeaderMessage,
	options,
	totalMatchesCount,
	maxSearchMatches,
	search,
	value
) {
	let totalMatchesCountHeaderMSG;
	if (!isNil(totalMatchesCountHeaderMessage)) {
		totalMatchesCountHeaderMSG = totalMatchesCountHeaderMessage;
	} else if (isNumber(totalMatchesCount)) {
		totalMatchesCountHeaderMSG =
			totalMatchesCount >= maxSearchMatches
				? t('Showing 1-{0} of more than {1}', options.length, maxSearchMatches)
				: t('Showing 1-{0} of {1}', options.length, totalMatchesCount);
	} else if (search === 'initial' || search === 'contains') {
		const resultsCount = getMatchesCount(options, search, value);
		totalMatchesCountHeaderMSG = t(
			'Showing 1-{0} of {1}',
			resultsCount,
			resultsCount
		);
	} else {
		totalMatchesCountHeaderMSG = t(
			'Showing 1-{0} of {1}',
			options.length,
			options.length
		);
	}

	return totalMatchesCountHeaderMSG;
}

function getConfigMessages(props, config = {}) {
	return [
		'loadingMessage',
		'totalMatchesCountHeaderMessage',
		'noResultsFoundMessaged'
	].reduce(
		(prev, name) => ({ ...prev, [name]: config[name] || props[name] }),
		{}
	);
}

NowTypeaheadBase.propTypes = {
	...controlledFieldPropTypes,

	/**
	 * Number of all possible matches for the given value, ignoring pagination
	 */
	totalMatchesCount: PropTypes.number,

	/**
	 * Array of options that represent valid choices
	 */
	options: PropTypes.arrayOf(PropTypes.object).isRequired,

	/**
	 * Array of options that represent recent choices
	 */
	recents: PropTypes.arrayOf(PropTypes.object).isRequired,

	/**
	 * Flag to indicate the options are in a loding state
	 */
	loading: PropTypes.bool,

	/**
	 * Callback function triggered when the user presses Enter
	 */
	onEnter: PropTypes.func,

	/**
	 * Message to show when the search results are being loaded
	 */
	loadingMessage: PropTypes.string,

	/**
	 * Message to show when no results are found
	 */
	noResultsFoundMessage: PropTypes.string,

	/**
	 * Header message that'll show up on the total count dropdown group
	 */
	totalMatchesCountHeaderMessage: PropTypes.string,

	/**
	 * Optional delimiter for secondary labels in options
	 */
	secondaryLabelDelimiter: PropTypes.string,

	/**
	 * Flag to Enable "contains" searches for reference autocompleters
	 */

	/**
	 * Flag to searching when clicking on field
	 */
	searchOnClick: PropTypes.bool,

	searchUsingStartsWith: PropTypes.bool,

	/**
	 * The maximum number of search results
	 */
	maxSearchMatches: PropTypes.number
};

NowTypeaheadBase.style = style;

NowTypeaheadBase.defaultProps = {
	options: [],
	recents: [],
	loading: false,
	initialActive: false,
	initialSelectedOption: -1,
	searchOnClick: true,
	includeHeaders: true
};

const NowTypeahead = withValueChangeCallbacks(
	{ stopBubbling: ['SN_RECORD_INPUT'] },
	NowTypeaheadBase
);

export default NowTypeahead;

/**
 *
 * Typeahead component that displays values off a given list.
 *
 * ```jsx
 * <React.Fragment>
 * 	<now-record-typeahead
 * 		value={"..."}
 * 		options={[{
 * 			value: 'VALUE 1'
 * 			secondary: [
 * 				{
 * 					value: 'VIP',
 * 					status: 'positive',
 * 					isHighlighted: 'true',
 * 					showIcon: true
 * 				},
 * 				'test'
 * 			],
 * 			displayValue: 'VALUE 1 - VIP'
 * 		}]}
 * 	/>
 * </React.Fragment>
 * ```
 *
 * @seismicElement now-record-typeahead
 * @summary Typeahead Component
 *
 * */
createCustomElement('now-record-typeahead', {
	type: react,
	view: ForwardProperties(NowTypeahead),
	initialState: { initialSearchComplete: false },
	styles: style,
	properties: {
		/**
		 * Causes the component to receive focus as soon as it has finished
		 * mounting.
		 * When passed as a string, "true" (case-sensitive) will be treated like
		 * boolean true.
		 * @type {string | boolean}
		 */
		autofocus: {},
		/**
		 * Configure the messages that the typeahead use.
		 *
		 * `type:string` loadingMessage - Message to show when the search results are being loaded.
		 *
		 * `type:string` totalMatchesCountHeaderMessage - Header message that'll show up on the total count dropdown group.
		 *
		 * `type:string` noResultsFoundMessage - Message to show when no results are found.
		 *
		 * @type {{loadingMessage: string, totalMatchesCountHeaderMessage: string, noResultsFoundMessage: string }}
		 */
		configMessages: {},
		/**
		 * Additional content describing the field, usually to provide further
		 * instruction to the user.
		 * @type {string}
		 */
		description: {},
		/**
		 * This is value of currently selected item.This is used as id in NDS now-typeahead
		 * @type {string}
		 */
		selectedItem: {},

		/**
		 * Specifies whether the current value is not valid.
		 * When passed as a string, "true" (case-sensitive) will be treated like
		 * boolean true.
		 * @type {string | boolean}
		 */
		invalid: { default: false },
		/**
		 * It's a label! All form controls need a label!
		 * @type {string}
		 */
		label: {},
		/**
		 * Flag to indicate the options are in a loding state
		 * @type {string | boolean}
		 * @private
		 */
		loading: { default: false },
		/**
		 * Message to show when the search results are being loaded
		 * @type {string}
		 * @private
		 */
		loadingMessage: {},
		/**
		 * Array of message objects that must contain type and message itself.
		 * @type {Array<{type: ("error"|"warning"|"success"|"liveUpdate"|"info"|"suggestion"), type:Array<string,Object<{string, getMessage:function}>> }>}
		 */
		messages: { default: [] },
		/**
		 * Name for the component. Any events dispatched by the component will use this name.
		 * @type {string}
		 */
		name: {},
		/**
		 * onBlur event callback
		 * @type {function}
		 * @private
		 */
		onBlur: {},
		/**
		 * onChange event callback
		 * @type {function}
		 * @private
		 */
		onChange: {},
		/**
		 * onClick event callback
		 * @type {function}
		 * @private
		 */
		onClick: {},
		/**
		 * onEnter event callback
		 * @type {function}
		 * @private
		 */
		onEnter: {},
		/**
		 * onFocus event callback
		 * @type {function}
		 * @private
		 */
		onFocus: {},
		/**
		 * Callback used when an intermediate value has changed.
		 * @type {function}
		 * @private
		 */
		onStagedValueChange: {},
		/**
		 * Callback used when the value has changed. Very similar to an `onChange`
		 * event, but this may also fire when the component formats its value
		 * onBlur.
		 *
		 * Params
		 *
		 * `type:object` event - The event payload
		 *
		 * `type:string` event.name - The name of the component that triggered the event
		 *
		 * `type:any` event.value - The new value for the component
		 *
		 * `type:string` event.displayValue - The human-readable represenation of the value
		 *
		 * `type:object` event.error - If the current value is invalid, this propery will be set. When the current value is valid, this property will be null.
		 *
		 * `type:string` event.error.type - Error type
		 *
		 * `type:string` event.error.message - Description of why the value is invalid
		 *
		 * @type {function}
		 * @private
		 */
		onValueChange: {},
		/**
		 * Array of options that represent valid choices
		 * @type {Array<Object>} - required
		 */
		options: { default: [] },

		/**
		 * Content to display when there is no suitable display value.
		 * @type {string}
		 */
		placeholder: {},
		/**
		 * Specifies that the user cannot modify the value of the component.
		 * When passed as a string, "true" (case-sensitive) will be treated like
		 * boolean true.
		 * @type {boolean}
		 */
		readonly: { default: false },
		/**
		 * Array of options that represent recent choices
		 * @type {Array<Object>} - required
		 */
		recents: { default: [] },
		/**
		 * Whether or not the user must fill in a value for this component.
		 * Can be specified as a boolean or the string "true"
		 * @type {boolean}
		 */
		required: { default: false },
		/**
		 * Specifies that the field should be rendered right-to-left.
		 * When passed as a string, "true" (case-sensitive) will be treated like
		 * boolean true.
		 * @type {boolean}
		 * @private
		 */
		reversed: { default: false },
		/**
		 * Flag to Enable "contains" searches for reference autocompleters
		 * @type {boolean}
		 */
		searchUsingStartsWith: { default: false },
		/**
		 * Optional delimiter for secondary labels in options
		 * @type {string}
		 */
		secondaryLabelDelimiter: {},
		/**
		 * Number of all possible matches for the given value, ignoring pagination
		 * @type {number}
		 */
		totalMatchesCount: {},
		/**
		 * Header message that'll show up on the total count dropdown group
		 * @type {string}
		 * @private
		 */
		totalMatchesCountHeaderMessage: {},
		/**
		 * Underlying data for the component.
		 * @type {string}
		 */
		value: {},
		/**
		 * formData object containing glide form data
		 * @type {object}
		 */
		formData: {},

		/**
		 * Declarative Action props
		 * @type {object}
		 */
		dAProps: {},

		/**
		 * Flag to searching when clicking on field
		 * @type {boolean}
		 */
		searchOnClick: { default: true },

		/**
		 * Controls how to filter the items as the user types into the input.
		 * - "managed" (default): no filtering will happen. Use this if you want to manually
		 *   filter or search for items as the user types.
		 * - "initial": items whose label or sublabel begins with the
		 *   input value will be displayed. This is case-insensitive.
		 * - "contains": items whose label or sublabel contains the input value
		 *   will be displayed. This is case-insensitive.
		 * @type {("managed"|"initial"|"contains")}
		 */

		search: {
			default: 'managed',
			schema: { type: 'string', enum: ['managed', 'initial', 'contains'] }
		}
	},
	actionHandlers: actionHandlers,
	dispatches: {
		/**
		 * Dispatched when the user has changed the input and wishes to run a query.
		 * @type {{ value: string }}
		 */
		'NOW_RECORD_TYPEAHEAD#STAGED_VALUE_CHANGED': {},
		/**
		 * Dispatched when a new value is selected.
		 * @type {{ value: string, error: { type: string, message: string } }}
		 */
		'NOW_RECORD_TYPEAHEAD#VALUE_CHANGED': {}
	},
	transformState
});
